/* purgecss start ignore */

.animate {
  -webkit-animation: animatezoom 0.8s;
  animation: animatezoom 0.8s;
}

@-webkit-keyframes animatezoom {
  from {
    -webkit-transform: scale(0);
    transform: scale(0)
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1)
  }
}

@keyframes animatezoom {
  from {
    -webkit-transform: scale(0);
    transform: scale(0)
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1)
  }
}

/* purgecss end ignore */


