/* purgecss start ignore */

.cartLabel {
  width: 20%;
  text-align: center;
  font-size: 0.75rem;
  line-height: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  --tw-text-opacity: 1;
  color: rgba(75, 85, 99, var(--tw-text-opacity));
}

.cartcontainerCart{
  padding-top: 100px;
}

.containerCart {
  padding-top: 100px;
  width: 100%;
}

@media (min-width: 640px) {
  .containerCart {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .containerCart {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {
  .containerCart {
    max-width: 1024px;
  }
}

@media (min-width: 1280px) {
  .containerCart {
    max-width: 1280px;
  }
}

@media (min-width: 1536px) {
  .containerCart {
    max-width: 1536px;
  }
}

/* purgecss end ignore */


