/* purgecss start ignore */

.loading{
  padding-top: 25%;
}

.products{
  padding-top: 2.5%;
  display:grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  row-gap: 40px;
  -webkit-column-gap: 50px;
  column-gap: 50px;
  margin-left: auto;
  margin-right: auto;
  width: 75%
}

@media (max-width: 1280px) {
  .products {
    display:grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 20px;
    -webkit-column-gap: 20px;
    column-gap: 20px;
    padding-left: 10%;
    padding-right: 10%;
  }
}

@media (max-width: 768px) {
  .products {
    display:grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 20px;
    -webkit-column-gap: 20px;
    column-gap: 20px;
    padding-left: 10%;
    padding-right: 10%;
    align-items: center;
  }
}

@media (max-width: 468px) {
  .products {
    display:grid;
    grid-template-columns: 1fr ;
    row-gap: 20px;
    -webkit-column-gap: 20px;
    column-gap: 20px;
    padding-left: 10%;
    padding-right: 10%;
  }
}

.product h1{
  font-weight: bold;
  font-size: 150%;
}

.product h2{
  font-style: italic;
  font-size: 150%;
  color: red;
}

.product h3{
  font-size: 120%;
  color: cadetblue;
}

.product button{
  margin-top: 2%;
  margin-bottom: 2%;
}

/* purgecss end ignore */




